import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getStaffCategories, deleteStaffCategory } from 'services/staffCategory';

class StaffCategory extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteStaffCategory(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Chức vụ',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#staff-category/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh mục chức vụ">
        <CartHeadAction>
          <BtnAddNew href="#staff-category/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getStaffCategories}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StaffCategory />
  </Page>
);



